function Actor({ userName, imageOption }) {
    const [userImage, setUserImage] = useState(`images/${userName}.png`);

    //   
    const handleImageUpload = (event) => {
        const file = event.target.files[0];
        if (file) {
            const reader = new FileReader();
            reader.onloadend = () => {
                const img = new Image();
                img.onload = () => {
                    const canvas = document.createElement('canvas');
                    const targetWidth = 150; //  
                    const targetHeight = 150; //  
                    canvas.width = targetWidth;
                    canvas.height = targetHeight;

                    const ctx = canvas.getContext('2d');
                    ctx.drawImage(img, 0, 0, targetWidth, targetHeight);

                    canvas.toBlob((blob) => {
                        const imageURL = URL.createObjectURL(blob);
                        setUserImage(imageURL); //   
                    }, 'image/jpeg');
                };
                img.src = reader.result;
            };
            reader.readAsDataURL(file);
        }
    };

    useEffect(() => {
        if (userName !== "default") {
            setUserImage(`images/${userName}.png?v=${Date.now()}`);
        }
    }, [userName]);

    return (
        <div>
            {/*   */}
            <label htmlFor="image-upload">
                <img
                    src={userImage}
                    alt={`${userName} `}
                    width="150" //  
                    height="150" //  
                    style={{ objectFit: "cover", cursor: imageOption ? "pointer" : "default" }}
                    onError={(e) => {e.target.src = ActorImage
                        console.log(` : /images/${userName}.png`);}} //       
                />
            </label>
            {/*   */}
            {imageOption && (
                <input
                    id="image-upload"
                    type="file"
                    accept="image/*"
                    style={{ display: "none" }}
                    onChange={handleImageUpload}
                />
            )}
        </div>
    );
}

export default Actor;
